.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
ul, li {
  list-style-type: none;
}
ul > li {
  display: inline-block;
}
.tile-container {
  background-color: white;
  box-shadow: 2px 2px 5px black;
  margin: 10px 10px;
}
.hero-container {
  background-color: white;
  box-shadow: 2px 2px 5px black;
}
.hero-container h3 {
  margin: 0 0;
  font-size: 12px;
  font-weight: 500;
}
.hero-container h1 {
  margin: 10px 0;
}
svg {
  /*background-color: #c3c3c3;*/
}
.tile-container-container {
  display: block;
}
.fever-container h1 {
  margin: 0 0!important;
}
.tile-container h3 {
  margin: 0 0;
  font-size: 12px;
  font-weight: 500;
}
.tile-container h1 {
  margin: 10px 0;
